import React, {Component} from "react";

export default class ComparatorRowComponent extends Component {

    constructor(props) {
        super(props);

    }

    renderVal(values) {
        return (
            (values != null && values.length > 0 && values[0] === true) ?
                <td><span className="g-icon g-icon--valid g-icon--xs-s"></span></td>
                :
                ((values != null && values.length > 0 && values[0] !== true) ? <td><span>{values[0]}</span></td>
                        :
                        ((values != null && values.length == 0) ? <td><span>-</span></td>
                                :
                                <td><span>-</span></td>
                        )
                )
        )
    }

    render() {
        let table =[];
        for(let i = 0; i<3-this.props.row.valueObject.length; i++){
            table.push(<td><span>-</span></td>)
        }

        return (
            <tr className={(this.props.row.hasDifferences && this.props.markDiff) ? "g-gray2-bgc" : ""}>
                <th>{this.props.row.attributeName}</th>
                {this.props.row.valueObject && this.props.row.valueObject.map(val =>
                    this.renderVal(val.value)
                )}
                {table}
            </tr>
        )
    }

}
